@font-face {
  font-family: Pfont;
  src: url("./font/PyeongChang-Regular.ttf");
}

@font-face {
  font-family: PfontB;
  src: url("./font/PyeongChang-Bold.ttf");
}

.teaLayout{
  width: 100vw;
  font-size: 14px;
  display: flex;
  justify-content: center;
  background-color: #eee;
  height: calc(var(--vh,1vh)*100);
}

.startPageLayout{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image : url('./StartView.png');
  width: 100%;
  max-width: 390px;
  height: 100vh;
}

.startLogo{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 30px;
  font-family: "PfontB";
  font-weight: 300;
  color: #ff5959;
  text-shadow: -1px 0px #ffe8e8, 0px 1px #ffe8e8, 1px 0px #ffe8e8, 0px -1px #ffe8e8;
}

.startButton{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #eeadad;
  color: black;
  border: none;
  border-radius: 8px;
  width: 120px;
  height: 50px;
  font-size: 25px;
  font-weight: 100;
  font-family: "Pfont";
  text-align: center;
  margin-top: 20px;
}

.questionLayout{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image : url('./StartView.png');
  width: 100%;
  max-width: 390px;
  height: 100vh;
  overflow: hidden;
}

.teaTitle{
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 90px;
  padding-right: 50px;
  font-size: 18px;
  font-weight: 900;
  font-family: "PfontB";
  text-align: right;
}

.teaTitle div:nth-child(1){
  display: flex;
  flex: 1;
  font-size: 28px;
  justify-content: center;
  padding-left: 50px;
  text-align: center;
}

.questionList{
  flex-direction: column;
  height: 100%;
  width: 390px;
  overflow: scroll;
  scrollbar-width: none;
}

.questionLayout::-webkit-scrollbar{
  display: none;
}

.questionBox{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80px;
  text-align: center;
  background-color: #DFFFDC;
  border: 1px solid #5ba05a;
  margin: 10px 22px 30px 22px;
  border-radius: 18px;
  font-family: Pfont;
  font-size: 16px;
  padding: 18px;
}

.answerLayout{
  width: 100%;
  padding-bottom: 40px;
  display: flex;
  flex-direction: column;
}

.answerBox{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  text-align: center;
  background-color: #ffffff;
  box-shadow: 2px 2px 0px #DFFFDC;
  border: 1px solid #82C781;
  margin: 0px 20px 15px 20px;
  border-radius: 16px;
  font-family: Pfont;
  font-size: 15px;
}

.resultLayout{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #eee;
  width: 100%;
  max-width: 390px;
  height: 100vh;
}

.resultTitle{
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 390px;
  height: 100vh;
  font-size: 18px;
  font-weight: 900;
}

.retryButton{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: aliceblue;
  color: black;
  border: none;
  border-radius: 8px;
  width: 120px;
  height: 50px;
  font-size: 25px;
  font-weight: 100;
  font-family: "Pfont";
  text-align: center;
  margin-top: 20px;
}